import { Toast, Notification, Modal } from 'mk-component'
import { fetch } from 'mk-utils'
import './mock.js' //mock

var _options = {}

//fetch
fetch.config({
	mock: true, //mock

	//fetchbefore,afterrestful api
	after: (response, url) => {
		if (response.result) {
            console.log(url, response)
            /*if(response.token){ //accessToken,
                fetch.config({token:response.token})
            }
            return response.value*/
		}
		else {
			Toast.error(response.error.message)
			throw { url, response }
		}
	}
})

function config(options) {
	Object.assign(_options, options)

	//key'^<key>$'app
    _options.apps && _options.apps.config({
        //'*': { webapi } //webapiweb
        'mk-app-root': {
            startAppName: 'mk-app-portal'
//            startAppName: 'mk-app-login'
        },
//        'mk-app-login': {
//            goAfterLogin: {
//                appName: 'mk-app-portal'
//            }
//        },
        'mk-app-portal': {
            menu: [{
                key: '1',
                name: '',
                appName: 'mk-app-portal-about',
                isDefault: true
            }, {
                key: '2',
                name: 'apps',
                isExpand: true,
                children: [{
                    key: '201',
                    name: '',
                    appName: 'mk-app-person-list'
                }, {
                    key: '202',
                    name: '',
                    appName: 'mk-app-person-card'
                }]
            }]
        }
    })

	_options.targetDomId = 'app' //react renderdom
	_options.startAppName = 'mk-app-portal' //app

	_options.toast = Toast //mk-meta-engine
	_options.notification = Notification //
	_options.modal = Modal //
	return _options
}

config.current = _options

export default config


