/**
 * webapi.js appweb
 * appappwebpaiconfig,app
 */


import { fetch } from 'mk-utils'

export default {
    user: {
        /**
         * @api {POST} /v1/user/login 
         * @apiName login
         * @apiGroup webapi__user
         * @apiPermission anyone
         * @apiDescription 
         * @apiParam  {Object} option json
         * @apiParamExample  {json}  
           {
               "mobile" : "13334445556", //
               "password" : "1" //
           }
         * @apiSuccessExample {Json}  
           {
                "result": true, //
                "token": "1,13334445556,1,13334445556,,", //token
                //
                "value": {  
                    "mobile": "13334445556",
                    "password": "1"
                }
            }
         */
        login: (option) => fetch.post('/v1/user/login', option)
    }
}


